// Creating own less file because directly in tsx it's not possible: https://github.com/webpack-contrib/mini-css-extract-plugin/issues/73
@import "~antd/lib/style/themes/default";
@import "~antd/lib/style/core/motion";
@import "~antd/lib/switch/style/index";
@import "~antd/lib/tag/style/index";
@import "~antd/lib/message/style/index";
@import "~antd/lib/tooltip/style/index";
@import "~antd/lib/spin/style/index";
@import "~antd/lib/modal/style/index";
@import "~antd/lib/popconfirm/style/index";
@import "~antd/lib/popover/style/index";
@import "~antd/lib/button/style/index";
@import "~antd/lib/avatar/style/index";
@import "~antd/lib/card/style/index";
@import "~antd/lib/form/style/index";
@import "~antd/lib/space/style/index";

// Fix select inputs in WordPress backend
.rcl-antd-select-single:not(.rcl-antd-select-customize-input) .rcl-antd-select-selector {
    box-sizing: border-box;
}

.rcl-antd-select-item {
    line-height: 2;
}

.rcl-antd-modal-mask,
.rcl-antd-modal-wrap {
    z-index: 99999999;
}

// Reset https://git.io/JLyJJ
.rcl-antd-input {
    padding: 4px 11px !important;
    line-height: 1.5715 !important;
    min-height: auto !important;
    box-shadow: none !important;
    border-radius: 2px !important;
    border: 1px solid #d9d9d9 !important;
    outline: none !important;
}

// Fix select dropdowns and messages in modals
.rcl-antd-select-dropdown, .rcl-antd-message {
    z-index: 105000000;
}

// Fix modal height and body overflow
.rcl-antd-modal-body {
    max-height: calc(100vh - 55px - 200px);
    overflow-y: auto;
}
@ant-prefix: rcl-antd;