.ant-message,
.ant-popover,
.ant-dropdown,
.ant-dropdown-menu-submenu-popup,
.ant-modal-mask,
.ant-modal-wrap {
    z-index: 160001;

    &.ant-message-bottom {
        bottom: 50px;
        top: initial !important;
    }
}

.rcl-container {
    display: block;
    float: left;
}

// Update notice
tr.rcl-update-notice {
    th {
        padding: 0;
    }

    .notice {
        margin: 0 20px 10px 26px;
        border-top: 1px solid #ffb900;
        box-shadow: 4px 4px 4px #ffb9001a, 1px 1px 0px #ffb900;

        p {
            margin: 0;
        }
    }
}

// WooCommerce activity panel compatibility
body.woocommerce-feature-enabled-activity-panels.activate-aiot {
    .rcl-container {
        .aiot-pad {
            margin-top: 70px;
        }
    }

    #wpbody {
        padding-left: 20px;

        #wpbody-content > .wrap {
            padding-left: 0;
        }
    }
}

// WP Job Openings panel compatibility
body.post-type-awsm_job_openings.activate-aiot,
body.post-type-awsm_job_application.activate-aiot {
    .rcl-container .aiot-pad {
        margin-top: 51px;
    }

    #wpbody {
        padding-left: 20px;

        #wpbody-content > .wrap {
            padding-left: 0;
        }
    }
}

@media all and(max-width: 700px) {
    body.activate-aiot {
        #wpbody {
            .rcl-container {
                width: 100% !important;
            }
        }
    }
}
